import React from 'react';
import {connect} from "react-redux";
import CanvasRenderer from "../canvasRenderer";
import {keyToColorPropertyComponent} from "./ColorPropertyComponent";


const mapStateToProps = state => {
    return {jsonOptions: state.jsonOptions};
};

class CanvasComponent extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.containerWidth = 973;
        this.containerHeight = 623;
        this.canvasRenderer = new CanvasRenderer(this.containerWidth * 2, this.containerHeight * 2)
    }

    componentDidMount() {
        this.updateCanvas();
    }

    componentDidUpdate() {
        this.updateCanvas();
    }

    updateCanvas() {
        let canvas = this.refs.canvas;
        canvas.style.width = this.containerWidth + "px";
        canvas.style.height = this.containerHeight + "px";
        const ctx = canvas.getContext('2d');
        this.canvasRenderer.render(ctx, this.props.jsonOptions);
    }

    handleClick = (e) => {
        let property = this.canvasRenderer.getResponsibleProperty(e.nativeEvent.offsetX * 2, e.nativeEvent.offsetY * 2);
        if (!property) return;
        let colorPropertyComponent = keyToColorPropertyComponent[property];
        if (!colorPropertyComponent) return;
        colorPropertyComponent.focusInput();
        window.alert(property);
    };

    render() {
        return (<div className="canvas-area">
            <canvas className="canvas-ide-component" width={this.containerWidth * 2} height={this.containerHeight * 2}
                    ref="canvas" onClick={this.handleClick}/>
        </div>);
    }
}


const RCanvasComponent = connect(mapStateToProps)(CanvasComponent);

export {RCanvasComponent as CanvasComponent}
